.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	padding-bottom: 29px;
	&__btn-menu {
		float: left;
		margin-top: 29px;
		z-index: 12; }
	&__inner {
		z-index: initial; }
	&__logo {
		margin: 27px 0 0 25px;
		float: left; }
	&__menu {
		float: left;
		z-index: 11; }
	&__list {
		float: right;
		margin-top: 26px;
		@include f('oeb', 12);
		text-transform: uppercase;
		@include md {
			margin-top: 28px; }
		li {
			vertical-align: top;
			display: inline-block;
			&:not(:last-child) {
				margin-right: 14px; } }
		a {
			position: relative;
			color: $black;
			letter-spacing: initial;
			transition: color .3s ease-in-out;
			display: block;
			@include md {
				font-size: 0; }
			&:after {
				content: "";
				display: block;
				position: absolute;
				top: 100%;
				left: 0;
				right: 0;
				height: 2px;
				background-color: $black;
				transform: scaleX(0);
				transition: transform .25s ease-in;
				transform-origin: right center; }
			@include hover {
				&:after {
					transform: scale(1);
					transform-origin: left center; }
				@include md {
					color: rgba($black, 0.6);
					&:after {
						transform: scale(0); } } } }
		.icon {
			font-size: 20px;
			vertical-align: middle;
			display: none;
			@include md {
				display: block; }
			@include sm {
				display: none; } }
		.icon-user {
			font-size: 19px; }
		.icon-cart {
			@include sm {
				display: block; } } } }

.logo {
	img {
		display: block; } }

.menu {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	transform: translateY(-100%);
	background-color: $white;
	@include sm {
		height: 100vh;
		overflow-y: auto; }
	.menu__inner {
		position: relative;
		padding-top: 26px;
		padding-bottom: 168px;
		@include sm {
			padding-top: 40%;
			padding-bottom: 40px; } }
	&__logo {
		position: absolute;
		left: 110px;
		bottom: 50px;
		@include sm {
			display: none; } }
	&__wrap {
		padding-left: 23.8%;
		@include clr;
		@include sm {
			padding-left: 38px; } }
	&__col {
		float: left;
		width: 33%;
		padding-left: 20px;
		&:not(:first-child) {
			@include sm {
				margin-top: 30px; } }
		@include sm {
			float: none;
			width: auto; }
		&_follow {
			@include sm {
				display: none; } } }
	&__title {
		margin-bottom: 87px;
		@include f('rt', 12);
		color: $lightGrayText;
		@include sm {
			display: none; } }
	&__list {
		color: $black;
		text-transform: uppercase;
		@include f('ob', 18);
		@include sm {
			@include f('ol'); }
		&_followUs {
			color: $lightGrayText;
			@include f('ol');
			text-transform: initial; } }
	&__item {
		&:not(:last-child) {
			margin-bottom: 12px;
			@include sm {
				margin-bottom: 8px; } } }
	&__link {
		position: relative;
		margin-left: -20px;
		padding: 0 20px;
		color: inherit;
		overflow: hidden;
		display: inline-block;
		@include hover {
			.menu__link-bg {
				animation: shadeAnimIn .5s cubic-bezier(.8,0,.2,1) forwards; } }
		.menu__link-bg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,.1);
			transform: translateX(103%);
			animation: shadeAnimOut .5s cubic-bezier(.8,0,.2,1) forwards; } } }
