.footer {
	position: relative;
	padding: 99px 0;
	@include sm {
		padding: 40px 0;
		background: $white; }
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		width: 50%;
		@include sm {
			display: none; } }
	&:before {
		left: 0;
		background: $white; }
	&:after {
		right: 0;
		background: $lightGray; }
	&__row {
		margin: 0 -15px;
		@include clr; }
	&__col {
		float: left;
		padding: 0 15px;
		width: 50%;
		@include sm {
			padding: 20px 15px;
			width: 100%; }
		// &:last-child
		// 	+sm
 }		// 		background: $lightGray
	&__nav {
		margin: 0 -25px 90px 0;
		@include clr;
		@include sm {
			margin-bottom: 50px; } }
	&__list,
	&__logo {
		float: left;
		width: 50%; }
	&__logo {
		padding: 4px 12px 0;
		a {
			display: inline-block;
			vertical-align: top;
			width: 66px;
			color: $black;
			@include f(rt,12,32,40); }
		img {
			display: inline-block;
			vertical-align: top;
			width: 100%; } }
	&__link {
		position: relative;
		display: inline-block;
		vertical-align: top;
		color: $black;
		@include f(rt,12,32,40);
		transition: opacity 0.15s ease-in-out;
		@include hover {
			opacity: 0.6; }
		// 	&:before
		// 		opacity: 1
		// &:before
		// 	content: ''
		// 	position: absolute
		// 	left: 0
		// 	right: 0
		// 	bottom: 5px
		// 	border-bottom: 1px solid currentColor
		// 	opacity: 0
 }		// 	transition: opacity 0.15s ease-in
	&__description {
		padding-left: 90px;
		margin: 0 auto;
		max-width: 320px;
		color: $text;
		@include f(rt,14,32,40);
		text-align: center;
		@include sm {
			display: none; }
		p {
			margin-bottom: 33px; } }
	&__social {
		padding: 3px 0 0 93px;
		color: $black;
		font-size: 0;
		text-align: center;
		@include sm {
			padding: 0; }
		li,
		a {
			display: inline-block;
			vertical-align: top; }
		a {
			padding: 7px 8px;
			color: $black;
			transition: opacity 0.15s ease-in-out;
			@include hover {
				opacity: 0.6; } }
		.icon {
			fill: currentColor;
			font-size: 32px; } } }
