[data-show-img*="parent"] {
	overflow: hidden;
	transform: translate3d(-100%,0,0);
	&.is-active {
		@include gpu;
		transition-delay: 0.3s;
		[data-show-img*="children"] {
			transition-delay: 0.3s;
			@include gpu; }
		[data-show-img*="opacity"] {
			opacity: 1; } } }

[data-show-img*="children"] {
	transform: translate3d(100%,0,0); }
[data-show-img*="opacity"] {
	opacity: 0; }

[data-show-img] {
	transition: transform 1s cubic-bezier(0.8,0,0.2,1), opacity 1.1s ease 167ms; }

[data-stagger*="from-"] {
	position: relative;
	opacity: 0;
	display: block; }
[data-stagger="from-top"] {
	transform: translateY(-30px); }

[data-hidden-anim-parent] {
	overflow: hidden; }
[data-show-onscroll] {
	&.is-active {
		[data-hidden-anim^="from-"] {
			transform: translateY(0);
			transition-delay: 0.3s; }
		.line[data-hidden-anim^="from-"] {
			transition-delay: 0s; } } }
[data-hidden-anim^="from-"] {
	position: relative;
	transition: transform .75s ease-out;
	display: block; }

[data-hidden-anim="from-top"] {
	transform: translateY(-100%); }
[data-hidden-anim="from-bottom"] {
	transform: translateY(100%); }
[data-hidden-anim="from-left"] {
	transform: translateX(-100%); }
[data-hidden-anim="from-right"] {
	transform: translateX(100%); }
