.box {
	display: block;
	color: $black;
	transform: translate3d(0,50px,0);
	transition: transform 1s, opacity 1.1s ease 167ms;
	&.is-active {
		@include gpu;
		.box__image-in {
			transform: scale(1); } }
	@include hover {
		.box {
			&__image {
				&-in {
					transform: scale(0.97); }
				&-picture {
					transform: scale(1.1); } } } }
	&__image {
		overflow: hidden;
		margin-bottom: 42px;
		&-in {
			position: relative;
			overflow: hidden;
			padding-bottom: 106%;
			height: 0;
			transform: scale(1.5);
			transition: transform 1s; }
		&-frame {
			position: absolute;
			top: -100px;
			left: 0;
			right: 0;
			bottom: -100px; }
		&-picture {
			height: 100%;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			transition: transform 1s; } }
	&__content {
		text-align: center; }
	&__title {
		margin-bottom: 3px;
		@include f(oeb, 12, 20); }
	&__meta {
		color: $lightGrayText;
		@include f(rt, 12, 20);
		letter-spacing: 0; } }
