.out {
	position: relative;
	overflow: hidden; }
.row {
	margin: 0 -10px;
	@include clr; }

.col {
	float: left;
	padding: 0 10px;
	&_lg {
		&-33 {
			width: 33.33%; }
		&-50 {
			width: 50%; } }
	&_md {
		&-100 {
			@include md {
				width: 100%; } } }
	&_sm {
		&-100 {
			@include sm {
				width: 100%; } } } }

.inner {
	position: relative;
	z-index: 10;
	padding: 0 105px;
	margin: 0 auto;
	max-width: 1366px;
	@include clr;
	@include md {
		padding: 0 50px; }
	@include sm {
		padding: 0 30px; } }
