body {
	background: $white;
	@include f('rl', 14);
	line-height: (28/14);
	letter-spacing: 0.03em;
	@include font-smoothing; }

.pull-left {
	float: left; }
.pull-right {
	float: right; }

.clear {
	@include clr; }

.title-lg {
	@include f('rt',80,88);
	@include md {
		@include f('rt',60,68); }
	@include sm {
		@include f('rt',40,48); } }

.title-sm {
	@include f(oeb,16,24,40);
	text-transform: uppercase;
	&_yellow-border {
		padding-bottom: 13px;
		border-bottom: 2px solid $yellow; } }

.sm-show {
	display: none;
	@include sm {
		display: block; } }

.line {
	padding: 0;
	margin: 13px 0 9px;
	height: 0;
	border: none;
	border-bottom: 2px solid $yellow; }
